@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
.pomodoro-clock{background-color:tomato;font-size:22px;font-family:sans-serif}.pomodoro-clock .pomodoro-clock-container{display:flex;flex-direction:column;justify-content:center;align-items:center;height:92vh;text-align:center;color:white}@media (max-width: 576px){.pomodoro-clock .pomodoro-clock-container{height:unset}}.pomodoro-clock .pomodoro-clock-container:after{content:"by oguzhanslmtemiz";margin:auto 0 0 0;font-size:1rem}.pomodoro-clock .clock-container{display:flex;align-items:center}@media (max-width: 576px){.pomodoro-clock .clock-container{flex-direction:column}}.pomodoro-clock .clock-container .break-session-container{height:50%}.pomodoro-clock .clock-container .container{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;margin:0 5px;padding:0 20px;background-color:rgba(255,255,255,0.1);border-radius:6px}@media (max-width: 576px){.pomodoro-clock .clock-container .container{padding:20px 20px}}.pomodoro-clock .clock-container .container .btn-container{display:flex;align-items:center;justify-content:space-between;width:100%}@media (max-width: 576px){.pomodoro-clock .clock-container .container .btn-container{margin-top:10px}}.pomodoro-clock .clock-container .container .btn-container button{background-color:white;border:none;color:tomato;cursor:pointer;border-radius:4px;padding:5px 10px;min-width:35px;font-weight:bold;box-shadow:#ebebeb 0px 6px 0px;transition:box-shadow ease 0.1s}.pomodoro-clock .clock-container .container .btn-container button:hover{opacity:0.85}.pomodoro-clock .clock-container .container .btn-container button:active{box-shadow:#adadad 0px 3px 0px}.pomodoro-clock .clock-container .container .btn-container button[disabled]{opacity:0.5;cursor:not-allowed}.pomodoro-clock .clock-container .timer{padding:20px 20px;width:230px}@media (max-width: 576px){.pomodoro-clock .clock-container .timer{margin:10px 0}}.pomodoro-clock .clock-container .timer #time-left{font-size:4em;margin:20px 0;font-family:"Anton", sans-serif}

